import { ComponentRef, Injectable } from '@angular/core';
import {
  AdaptDockedPanelService,
  AdaptModalService,
  DismissReasons,
  DockedPanelInstance,
  ModalDeferred
} from '@bmc-ux/adapt-angular';
import { RxModalComponent } from './modal.component';
import { IDialogApi, IModalConfig, IPromptResponse } from './modal.config.interfaces';
import { RX_MODAL } from './modal.constant';
import { constant, some, toArray } from 'lodash';
import { TranslateService } from '@ngx-translate/core';

@Injectable({
  providedIn: 'root'
})
export class RxModalService {
  constructor(
    private adaptModalService: AdaptModalService,
    private translateService: TranslateService,
    private adaptDockedPanelService: AdaptDockedPanelService
  ) {}

  isAnyDockedPanelDirty(): boolean {
    return some(this.adaptDockedPanelService.openedPanels, (dockedPanelInstance: DockedPanelInstance) =>
      (dockedPanelInstance.contentInstanceRef as any)?.isDirty?.()
    );
  }

  isAnyModalDirty(): boolean {
    return some(toArray(this.adaptModalService.openedModals.values()), (modalRef: ComponentRef<any>) =>
      modalRef.instance.contentInstanceRef?.isDirty?.()
    );
  }

  confirm(modalConfig: IModalConfig, allowDismiss = true): Promise<boolean> {
    this.setButtons(
      modalConfig,
      this.translateService.instant('com.bmc.arsys.rx.client.common.yes.label'),
      this.translateService.instant('com.bmc.arsys.rx.client.common.no.label')
    );

    return this.adaptModalService
      .open({
        content: RxModalComponent,
        data: { modalType: RX_MODAL.modalTypes.confirm, modalConfig },
        type: modalConfig.modalStyle,
        isDialog: true,
        beforeDismiss: () => allowDismiss
      })
      .then((result: any) => result === true)
      .catch(constant(false));
  }

  alert(modalConfig: IModalConfig): ModalDeferred {
    this.setButtons(modalConfig, this.translateService.instant('com.bmc.arsys.rx.client.common.ok.label'));

    return this.adaptModalService.open({
      content: RxModalComponent,
      data: { modalType: RX_MODAL.modalTypes.alert, modalConfig },
      type: modalConfig.modalStyle,
      isDialog: true
    });
  }

  prompt(modalConfig: IModalConfig): Promise<IPromptResponse> {
    this.setButtons(
      modalConfig,
      this.translateService.instant('com.bmc.arsys.rx.client.common.ok.label'),
      this.translateService.instant('com.bmc.arsys.rx.client.common.cancel.label')
    );

    return this.adaptModalService
      .open({
        content: RxModalComponent,
        data: { modalType: RX_MODAL.modalTypes.prompt, modalConfig },
        type: modalConfig.modalStyle
      })
      .then((result: any) => {
        return result?.response ? result : { response: false };
      })
      .catch(
        constant({
          response: false
        })
      );
  }

  private setButtons(modalConfig: IModalConfig, confirmButton: string, dismissButton?: string) {
    if (modalConfig.buttons) {
      modalConfig.buttons.confirmButton = modalConfig.buttons.confirmButton || confirmButton;

      modalConfig.buttons.dismissButton = modalConfig.buttons.dismissButton || dismissButton;
    } else {
      modalConfig.buttons = {
        confirmButton,
        dismissButton
      };
    }
  }

  private open(config: any, isModal: boolean): ModalDeferred | Promise<any> {
    let api: IDialogApi | null = null;
    const onApiReady = config.data?.onApiReady;

    const updatedConfig = {
      ...config,
      data: {
        ...config.data,
        onApiReady: (dialogApi: IDialogApi) => {
          onApiReady?.(dialogApi);
          api = dialogApi;
        }
      },
      beforeDismiss: (reason: any) => {
        const canClose = !Object.values(DismissReasons).includes(reason);

        if (!canClose) {
          api?.dismissDialog();
        }

        return canClose;
      }
    };

    const result = isModal
      ? this.adaptModalService.open(updatedConfig)
      : this.adaptDockedPanelService.open(updatedConfig);

    return result.then(
      (data) => {
        api = null;

        return data;
      },
      (reason) => {
        api = null;

        return Promise.reject(reason);
      }
    );
  }

  /**
   * @deprecated The method is deprecated, use openModal instead
   */
  openDialog(config: any): ModalDeferred | Promise<any> {
    return this.open(config, true);
  }

  openModal(config: any): ModalDeferred | Promise<any> {
    return this.open(config, true);
  }

  openDockedPanel(config: any): ModalDeferred | Promise<any> {
    return this.open(config, false);
  }
}
