import { Inject, Injectable, Injector, OnInit } from '@angular/core';
import { ActiveModalRef, DockedPanelContext } from '@bmc-ux/adapt-angular';
import { RxUtilityModalsService } from './utility-modals.service';

@Injectable()
export class RxModalClass implements OnInit {
  private rxUtilityModalsService: RxUtilityModalsService;
  private _isDirty = false;

  protected dialogApi = {
    dismissDialog: this.dismissDialog.bind(this),
    isDirty: this.isDirty.bind(this)
  };

  constructor(
    @Inject(DockedPanelContext || ActiveModalRef) protected context: DockedPanelContext | ActiveModalRef,
    protected injector: Injector
  ) {
    this.rxUtilityModalsService = injector.get(RxUtilityModalsService);
  }

  get allowDismiss(): boolean {
    return this.context.getData().allowDismiss ?? true;
  }

  set allowDismiss(value: boolean) {
    this.context.getData().allowDismiss = value;
  }

  markAsDirty(): void {
    this._isDirty = true;
  }

  isDirty(): boolean {
    return this._isDirty;
  }

  ngOnInit() {
    this.context.getData().onApiReady(this.dialogApi);
  }

  dismissDialog(): void {
    if (this.allowDismiss === false) {
      return;
    }

    if (this.isDirty()) {
      this.rxUtilityModalsService.confirmUnsavedChanges().then((result) => {
        if (result) {
          this.context.dismiss(null);
        }
      });
    } else {
      this.context.dismiss(null);
    }
  }
}
