import { Injectable } from '@angular/core';
import { RxModalService } from './modal.service';
import { RX_MODAL } from './modal.constant';
import { TranslateService } from '@ngx-translate/core';

@Injectable({
  providedIn: 'root'
})
export class RxUtilityModalsService {
  private confirmationResult: Promise<boolean> | null = null;

  constructor(private rxModalService: RxModalService, private translateService: TranslateService) {}

  confirmExternalChange(message: string): Promise<boolean> {
    return this.rxModalService.confirm({
      title: this.translateService.instant('com.bmc.arsys.rx.client.common.warning.label'),
      modalStyle: RX_MODAL.modalStyles.warning,
      message: `${message} ${this.translateService.instant(
        'com.bmc.arsys.rx.client.common.continue-confirmation.message'
      )}`
    });
  }

  confirm(
    message: string,
    title: string = this.translateService.instant('com.bmc.arsys.rx.client.common.warning.label'),
    style = RX_MODAL.modalStyles.warning
  ): Promise<boolean> {
    return this.rxModalService.confirm({
      title,
      modalStyle: style,
      message
    });
  }

  confirmUnsavedChanges(): Promise<boolean> {
    // allow to open only one confirmation dialog
    if (!this.confirmationResult) {
      this.confirmationResult = this.rxModalService
        .confirm(
          {
            title: this.translateService.instant('com.bmc.arsys.rx.client.common.warning.label'),
            modalStyle: RX_MODAL.modalStyles.warning,
            message: this.translateService.instant('com.bmc.arsys.rx.client.common.unsaved-data.message')
          },
          false
        )
        .then((result) => {
          this.confirmationResult = null;

          return result;
        });
    }

    return this.confirmationResult;
  }
}
