import { Component, Input, OnChanges, OnDestroy, OnInit, SimpleChanges } from '@angular/core';
import { NEVER } from 'rxjs';
import { BusyConfig } from '@bmc-ux/adapt-angular';

@Component({
  selector: 'rx-busy-indicator',
  templateUrl: './busy-indicator.component.html'
})
export class RxBusyIndicatorComponent implements OnInit, OnChanges, OnDestroy {
  @Input()
  options?: BusyConfig;

  private defaultSubscription = NEVER.subscribe();

  private defaultOptions: BusyConfig = {
    busy: this.defaultSubscription,
    backdrop: true,
    message: '',
    minDuration: 0,
    delay: 0,
    loaderType: 'section'
  };

  config: BusyConfig;

  constructor() {
    this.config = this.defaultOptions;
  }

  ngOnInit(): void {
    this.updateConfig();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.options && !changes.options.firstChange) {
      this.updateConfig();
    }
  }

  ngOnDestroy(): void {
    this.defaultSubscription.unsubscribe();
  }

  private updateConfig(): void {
    this.config = {
      ...this.defaultOptions,
      ...this.options
    };
  }
}
