import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

import { IValidationIssue, IValidationIssueSection, ValidationIssueType } from './validation-issues.types';

@Component({
  selector: 'rx-validation-issues',
  templateUrl: './validation-issues.component.html',
  styleUrls: ['./validation-issues.component.scss']
})
export class RxValidationIssuesComponent implements OnInit {
  @Input()
  definitionTypeDisplayName: string;

  @Input()
  issueSections: IValidationIssueSection[] = [];

  @Output()
  correctIssue: EventEmitter<IValidationIssue> = new EventEmitter();

  emptyText: string;

  ValidationIssueType = ValidationIssueType;

  onCorrectIssue(validationIssue: IValidationIssue) {
    this.correctIssue.emit(validationIssue);
  }

  constructor(private translateService: TranslateService) {}

  ngOnInit(): void {
    this.emptyText = this.translateService.instant('com.bmc.arsys.rx.client.designer.definition-is-valid.message', {
      definitionTypeDisplayName: this.definitionTypeDisplayName.toLowerCase()
    });
  }
}
