import { Directive, Input, ElementRef, AfterViewInit, OnChanges, Renderer2 } from '@angular/core';

@Directive({
  selector: '[rxVerticalTextTruncate]'
})
export class RxVerticalTextTruncateDirective implements AfterViewInit, OnChanges {
  constructor(private el: ElementRef, private renderer: Renderer2) {}

  textToTruncate = '';

  @Input()
  rxVerticalTextTruncate = '';

  ngAfterViewInit(): void {
    this.truncateText();
  }

  ngOnChanges(): void {
    this.truncateText();
  }

  truncateText(): void {
    const el: HTMLElement = this.renderer.selectRootElement(this.el.nativeElement, true);

    this.textToTruncate = this.rxVerticalTextTruncate;

    if (this.textToTruncate) {
      const words: string[] = this.textToTruncate.trim().replace(/\n|\r/g, '').split(' ');

      let resultValue = '';

      for (const word of words) {
        const innerText = el.innerText;

        resultValue = `${innerText} ${word}`;

        if (el.scrollHeight > el.offsetHeight) {
          // Subtract 3 characters, since we concatenate ...
          resultValue = `${innerText.slice(0, innerText.length - 3)}...`;

          break;
        }
      }

      this.renderer.setProperty(el, 'innerText', resultValue);
    }
  }
}
